* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #bfb8af;
}

.breed-select {
  margin-bottom: 70px;
  position: relative;
  top: 20px;
  left: 40%;
}

.error {
  color: red;
  font-size: 20px;
  font-weight: 700;
}

.is-hidden {
  opacity: 0;
}

.cat-info img {
  display: inline-block;
}

.cat-info {
  font-size: 25px;
  font-style: italic;
  font-weight: 500;
  display: flex;
}

.cat-titles {
  margin-bottom: 100px;
  font-size: 60px;
  font-weight: 600;
}

.cat-description p {
  margin-bottom: 50px;
}

.cat-description {
  padding-left: 50px;
  padding-right: 50px;
}

.cat-temp {
  font-weight: 600;
}

.loader {
  perspective: 1000px;
  width: 60px;
  height: 60px;
  color: #0719da;
  border-radius: 50%;
  transform: rotateZ(45deg);
}

.loader:before, .loader:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: calc(50% - 30px);
  left: calc(50% - 30px);
  transform: rotateX(70deg);
}

.loader:after {
  color: #e9e50a;
  animation-delay: .4s;
  transform: rotateY(70deg);
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%)rotateZ(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotateZ(360deg);
  }
}

@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotate(-360deg);
  }
}

@keyframes spin {
  0%, 100% {
    box-shadow: .2em 0;
  }

  12% {
    box-shadow: .2em .2em;
  }

  25% {
    box-shadow: 0 .2em;
  }

  37% {
    box-shadow: -.2em .2em;
  }

  50% {
    box-shadow: -.2em 0;
  }

  62% {
    box-shadow: -.2em -.2em;
  }

  75% {
    box-shadow: 0 -.2em;
  }

  87% {
    box-shadow: .2em -.2em;
  }
}



